* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}

.background-login { 
  text-align: center;
  /* The image used */
  background-image: url("/images/background-DNA.jpg");
  /* Full height */
  height: 50vh; 
  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.login {
  font-family: 'Scada', sans-serif;
  font-weight: bold;
  color: aliceblue;
  width: 50%;
  height: 10vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.form-login {
  display: flex;
  flex-wrap: wrap;
  padding-top: 250px;
  font-family: 'Scada', sans-serif;
  font-size: 1.2em;
  flex-direction: column;
  align-items: center;
}

.form-login > div > input {
  border-radius: 10px;
  padding: 5px 5px;
  margin-left: 10px;
}

.form-login > div {
  padding: 10px 0;
}

.btn-login {
  border: none;
  font-family: 'Scada', sans-serif;
  font-weight: bold;
  color: #1C3337;
  font-size: 1em;
  border-radius: 10px;
  padding: 5px 10px;
  margin-top: 20px;
  margin-left: 20%;
}

.LoginCare {
  margin-top:350px;
}

.LoginCare > .p {
  display: inline;
  color: #4BAAD3;
  margin-left: 10px;
  margin-right: 10px;
}

.Sociallogin > img {
  height: 6vh;
  margin-top: 30px;
  margin-left: 15px;
  margin-right: 15px;
}

@media screen and (max-width: 768px) {
  .background-login {
    margin-top: 20px;
  }
  .form-login {
    padding-top: 400px;
    font-size: 1.5em;
    margin-left: 25px;
  }
  .LoginCare {
    margin-top:600px;
  }
}
@media screen and (max-width: 414px) {
  .form-login {
    padding-top: 300px;
    font-size: 1.5em;
    margin-left: 207px;
  }
  .LoginCare {
    margin-top:350px;
    font-size: 0.8em;
  }
  .Sociallogin > img {
    height: 4vh;
  }
}